body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-align: center;
}

.currency-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  height: 80vh;
  overflow: scroll;
}

.currency-list::-webkit-scrollbar { 
  display: none; 
  overflow: hidden;
}

.currency-list__card {
  width: 200px;
  margin: 10px 0;
  background-color: #b4b4ff; 
  padding: 10px 0 20px;
}

.currency-list__card--detail {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  font-size: 0.9em;
}

.currency-list__card--detail span {
  font-size: 0.75em;
}

.searchBox {
  width: 200px;
  font-size: 36px;
}


